.nav__list {
  margin-right: 1.5em;
  display: flex;
}
.nav__list-item {
  margin-left: 1.5em;
}

.nav__list-item span {
  color: var(--clr-primary);
  left: 0; /* Align the line with the text */
  padding: 0.5em;
  margin-bottom: 1.5em;
  border: 2px solid var(--clr-primary); /* Style the line */
  transition: transform 0.2s ease-in-out;
}

.nav__list-item span:hover{
  background-color: var(--clr-primary);
  color: var(--clr-bg);
}

.app .nav__hamburger {
  display: none;
}

.nav__theme {
  margin-top: 0.4em;
}

@media (max-width: 600px) {
  .nav__list {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  .nav__list-item {
    margin: 0.5em 0;
  }

  .app .nav__hamburger {
    display: flex;
    z-index: 2;
    margin-left: 0.8em;
  }
}
